html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  background-position: center;
  background-size: cover;
  padding: 3rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 20rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__heading {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    max-width: 35rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
  }
}
@media (min-width: 1200px) {
  .header__imagen {
    max-width: 60rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.propiedades {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .propiedades {
    margin-top: 10rem;
  }
}
.propiedades__heading {
  font-weight: 400;
}
.propiedades__heading span {
  font-weight: 700;
}
.propiedades__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.propiedades__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .propiedades__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.propiedad {
  background-color: #FFFFFF;
}
.propiedad__imagen {
  aspect-ratio: 10/6;
  background-size: auto 150%;
  position: relative;
}
.propiedad:nth-child(1) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_1.jpg");
}
.propiedad:nth-child(2) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_2.jpg");
}
.propiedad:nth-child(3) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_3.jpg");
}
.propiedad:nth-child(4) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_4.jpg");
}
.propiedad:nth-child(5) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_5.jpg");
}
.propiedad:nth-child(6) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_6.jpg");
}
.propiedad__nombre {
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.propiedad__contenido {
  padding: 2rem;
}
.propiedad__texto {
  margin-bottom: 0;
}
.propiedad__precio {
  font-size: 3rem;
  margin: 0;
}
.propiedad__enlace {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .propiedad__enlace {
    width: auto;
  }
}
.propiedad__enlace:hover {
  background-color: #283142;
}

.destacada {
  margin-top: 5rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
  background-size: 100%, cover;
  background-position: center, -50rem -30rem;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .destacada {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
  }
}
.destacada__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .destacada__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.destacada__contenido {
  padding: 5rem;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .destacada__contenido {
    padding: 10rem 5rem;
    grid-column: 2/3;
  }
}
.destacada__heading {
  color: #FFFFFF;
  text-transform: uppercase;
}
.destacada__texto {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.destacada__precio {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 700;
}
.destacada__enlace {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #3b4862;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .destacada__enlace {
    width: auto;
  }
}
.destacada__enlace:hover {
  background-color: #e6e6e6;
}

.testimoniales-blog {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .testimoniales-blog {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .testimoniales-blog__blog {
    grid-column: 2/4;
  }
}
.testimoniales-blog__heading {
  font-weight: 400;
}
.testimoniales-blog__heading span {
  font-weight: 700;
  display: block;
}
.testimoniales-blog__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}

.testimonial {
  text-align: center;
}
.testimonial__imagen {
  width: 20rem;
  margin: 0 auto;
}
.testimonial__contenido {
  background-color: #FFFFFF;
  margin-top: -10rem;
  padding: 10rem 2rem 2rem 2rem;
}
.testimonial__nombre {
  font-size: 2.4rem;
  font-weight: 700;
}
.testimonial__texto::before {
  content: "";
  background-image: url(../img/quote.svg);
  display: inline-block;
  width: 4rem;
  height: 4rem;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 1rem;
}

.entrada {
  background-color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 768px) {
  .entrada__imagen-contenedor {
    grid-column: 1/3;
  }
}
.entrada__imagen {
  height: 30rem;
  width: 100%;
  object-fit: cover;
}
.entrada__contenido {
  padding: 1rem;
}
@media (min-width: 768px) {
  .entrada__contenido {
    grid-column: 3/6;
  }
}
.entrada__meta {
  font-size: 1.4rem;
}
.entrada__enlace {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .entrada__enlace {
    width: auto;
  }
}
.entrada__enlace:hover {
  background-color: #283142;
}
@media (min-width: 768px) {
  .entrada__enlace {
    display: inline-block;
  }
}

.footer {
  background-color: #3b4862;
  padding-top: 5rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__heading {
  color: #FFFFFF;
  font-size: 2rem;
  text-transform: uppercase;
}
.footer__texto {
  color: #FFFFFF;
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
}
.footer__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2¿1rem;
}
.footer__link:last-of-type {
  margin-bottom: 0;
}
.footer__copyright {
  background-color: #283142;
  text-align: center;
  margin: 5rem 0 0 0;
  padding: 2rem;
  color: #FFFFFF;
}